.ItemStyle{
    border: 1px solid #e1e1e1;
    height: 90px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto 33px;
    filter: grayscale(1);
    transition: all .3s ease-out;
    &:hover{
        box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, .15);
        filter: grayscale(0);
        transition: all .3s ease-out;
    }
}
.brend-block, .customer-block{
    margin-bottom: 35px;
    &__inner{
        padding: 0 35px;
        position: relative;
    }
    &__slider{
        position: static;
        .swiper-button-prev{
            left: 8px;
        }
        .swiper-button-next{
            right: 8px;
        }
        .bx-ie10, .bx-ie11 {
            top: calc(50% - 10px);
        }
        .swiper-button-prev, .swiper-button-next{
            width: 13px;
            height: 27px;
            top: calc(50% + 10px);
            &::after{
                display: inline-block;
                font-size: 27px;
                width: 13px;
                height: 27px;
            }
        }
        .swiper-slide{
            margin-top: 10px;
            margin-bottom: 10px;
        }
    }
    .section-title{
        text-align: left;
        margin-bottom: 15px;
    }
}
.customer-block{
    .ItemStyle{
        background-size: auto 65px;
    }
}
@media(max-width: 767px){
    .brend-block, .customer-block{
        margin-bottom: 20px;
    }
    .brend-block .section-title, .customer-block .section-title {
        margin-bottom: 15px;
    }
    .brend-block__inner, .customer-block__inner {
        padding: 0 24px;
    }
}
@media(max-width: 575px) {
    .brend-block__inner, .customer-block__inner {
        padding: 0 30px;
    }
}